import {
  REFRESH_DATA,
  DELETE_ITEM,
  SELECT_ITEM,
  FILTERS_CONFIG,
  REFRESH_FILTERED_DATA,
  REFRESH_SHOWN_DATA,
  TOGGLE_VIRTUAL_TABLE,
} from './constantas/action-types';

const refreshData = payload => {
  return { type: REFRESH_DATA, payload };
};

const deleteItem = payload => {
  return { type: DELETE_ITEM, payload };
};

const selectItem = payload => {
  return { type: SELECT_ITEM, payload };
};

const refreshFilterConfig = payload => {
  return { type: FILTERS_CONFIG, payload };
};

const refreshFilteredData = payload => {
  return { type: REFRESH_FILTERED_DATA, payload };
};

const refreshShownData = payload => ({ type: REFRESH_SHOWN_DATA, payload });
const toggleVirtualTable = payload => ({ type: TOGGLE_VIRTUAL_TABLE, payload });

export {
  refreshData,
  deleteItem,
  selectItem,
  refreshFilterConfig,
  refreshFilteredData,
  refreshShownData,
  toggleVirtualTable,
};
