/* eslint-disable no-shadow */
import React from 'react';
import { connect } from 'react-redux';
import { makeStyles } from '@material-ui/core';

import { toggleVirtualTable, refreshData, refreshFilteredData } from '../../actions';
import TableToolbar from './table-toolbar';
import Switcher from '../switcher';
import VirtualTable from './table-virtual';
import SimpleTable from './table-simple';

const useStyles = makeStyles(theme => ({
  root: {
    position: 'relative',
    width: '100%',
    height: '100%',
    maxWidth: 1200,
    minWidth: 400,
    boxShadow: theme.shadows[3],
  },
}));
const ConnectedDataGrid = ({
  isVirtual,
  toggleVirtualTable,
  selected,
  refreshData,
  initialData,
}) => {
  const classes = useStyles();
  const [isVirtualChecked, setIsVirtualChecked] = React.useState(isVirtual);

  const toggleChecked = () => {
    setIsVirtualChecked(!isVirtualChecked);
    toggleVirtualTable({ isVirtual: !isVirtual });
  };

  const handleDelete = () => {
    const newArr = initialData.filter(el => !selected.includes(el.id));
    refreshData({ initialData: newArr, selected: [] });
    refreshData({ filteredData: newArr });
  };

  return (
    <div className={classes.root}>
      <Switcher checked={isVirtualChecked} toggleChecked={toggleChecked} />
      <TableToolbar handleDelete={handleDelete} />
      {isVirtualChecked ? <VirtualTable /> : <SimpleTable />}
    </div>
  );
};

const mapStateToProps = ({ isVirtual, initialData, selected }) => ({
  isVirtual,
  initialData,
  selected,
});

const mapDispatchToProps = dispatch => {
  return {
    toggleVirtualTable: data => dispatch(toggleVirtualTable(data)),
    refreshData: data => dispatch(refreshData(data)),
    refreshFilteredData: data => dispatch(refreshFilteredData(data)),
  };
};

const DataGrid = connect(mapStateToProps, mapDispatchToProps)(ConnectedDataGrid);

export default DataGrid;
