import {
  REFRESH_DATA,
  SELECT_ITEM,
  FILTERS_CONFIG,
  REFRESH_FILTERED_DATA,
  REFRESH_SHOWN_DATA,
  TOGGLE_VIRTUAL_TABLE,
} from './constantas/action-types';
import data from './utils/data';
import { SORT_CONFIG } from './constantas/settings';

const initialState = {
  initialData: data,
  filteredData: data,
  shownData: [],
  selected: [],
  filterConfig: SORT_CONFIG,
  isVirtual: true,
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case REFRESH_DATA:
      return { ...state, ...action.payload };
    case SELECT_ITEM:
      return { ...state, ...action.payload };
    case FILTERS_CONFIG:
      return { ...state, ...action.payload };
    case REFRESH_FILTERED_DATA:
      return { ...state, ...action.payload };
    case REFRESH_SHOWN_DATA:
      return { ...state, ...action.payload };
    case TOGGLE_VIRTUAL_TABLE:
      return { ...state, ...action.payload };
    default:
      return state;
  }
};

export default reducer;
