/* eslint-disable no-shadow */
/* eslint-disable react/prop-types */
import React, { useState, createRef, useEffect } from 'react';
// import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';

import { refreshData, selectItem } from '../../actions';
import getData from '../../utils/get-data';
import { SETTINGS, DATAGRID_SORT_CONFIG } from '../../constantas/settings';
import { getComparator, stableSort } from '../../utils/sort-functions';
import refreshLS from '../../utils/save-in-LS';
import TableHeader from './table-head';
import RowTemplate from './row-template';
import store from '../../store';

const setInitialState = settings => {
  const { itemHeight, amount, tolerance, minIndex, maxIndex, startIndex } = settings;
  const viewportHeight = amount * itemHeight;
  const totalHeight = (maxIndex - minIndex + 1) * itemHeight;
  const toleranceHeight = tolerance * itemHeight;
  const bufferHeight = viewportHeight + 2 * toleranceHeight;
  const bufferedItems = amount + 2 * tolerance;
  const itemsAbove = startIndex - tolerance - minIndex;
  const topPaddingHeight = itemsAbove * itemHeight + 60;
  const bottomPaddingHeight = totalHeight - topPaddingHeight;
  const initialPosition = topPaddingHeight + toleranceHeight;
  return {
    settings,
    viewportHeight,
    totalHeight,
    toleranceHeight,
    bufferHeight,
    bufferedItems,
    topPaddingHeight,
    bottomPaddingHeight,
    initialPosition,
  };
};

const ConnectedVirtualTable = props => {
  const useStyles = makeStyles({
    virtScroller: {
      height: 900,
      overflow: 'auto',
      minHeight: 500,
    },
  });
  const styles = useStyles();

  const config = JSON.parse(localStorage.getItem(DATAGRID_SORT_CONFIG));

  const { selected, shownData, filteredData, isVirtual } = props;
  const [state, setState] = useState(setInitialState(SETTINGS));
  const [orderBy, setOrderBy] = useState(config.orderBy);
  const [order, setOrder] = useState(config[orderBy].sortDirection);

  const [scrollVert, setScrollVert] = useState(0);
  const [scrollLeftState, setScrollLeftState] = useState(0);
  const [shownDataState, setShownDataState] = useState(isVirtual ? shownData : filteredData);

  const viewportElement = createRef();
  const { itemHeight, minIndex, tolerance } = SETTINGS;

  const runScroller = ({ target }) => {
    const { totalHeight, toleranceHeight, bufferedItems } = state;

    const { scrollTop, scrollLeft } = target;
    setScrollLeftState(scrollLeft);

    if (scrollVert !== viewportElement.current.scrollTop) {
      const index = minIndex + Math.floor((scrollTop - toleranceHeight) / itemHeight);
      const data = getData(index, bufferedItems, minIndex, filteredData);
      const topPaddingHeight = Math.max((index - minIndex) * itemHeight, 0) + 60;
      const bottomPaddingHeight = Math.max(
        totalHeight - topPaddingHeight - data.length * itemHeight,
        0
      );
      props.refreshData({ shownData: data });
      setState({ ...state, topPaddingHeight, bottomPaddingHeight });
    }

    setScrollVert(viewportElement.current.scrollTop);
  };

  const handleRequestSort = property => {
    const isAsc = orderBy === property && order === 'asc';

    const newOrder = isAsc ? 'desc' : 'asc';
    setOrder(newOrder);
    setOrderBy(property);
    const sortedData = stableSort(filteredData, getComparator(newOrder, property));

    const totalHeight = viewportElement.current.clientHeight;
    const amount = Math.ceil(totalHeight / itemHeight);
    const data = getData(0, amount + tolerance, 0, sortedData);

    config.orderBy = property;
    config[property].sortDirection = newOrder;
    refreshLS(config);

    props.refreshData({ shownData: data });
  };
  store.subscribe(() => {
    const data = store.getState().filteredData;
    setShownDataState(data);
  });

  useEffect(() => {
    const data = getData(0, 25, 0, filteredData);
    props.refreshData({ shownData: data });
    if (!state.initialPosition) {
      runScroller({ target: { scrollTop: 0 } });
    }
  }, [shownDataState, filteredData]);

  const { topPaddingHeight, bottomPaddingHeight } = state;

  return (
    <div className={styles.virtScroller} ref={viewportElement} onScroll={runScroller}>
      <TableHeader
        numSelected={selected.length}
        order={order}
        orderBy={orderBy}
        onRequestSort={handleRequestSort}
        rowCount={shownData.length}
        scrollLeft={scrollLeftState}
      />
      <div style={{ height: topPaddingHeight }} />
      {shownData.map(item => (
        <RowTemplate item={item} key={item.id} scrollLeft={scrollLeftState} />
      ))}
      <div style={{ height: bottomPaddingHeight }} />
    </div>
  );
};

const mapStateToProps = ({ filteredData, shownData, selected }) => ({
  filteredData,
  shownData,
  selected,
});

const mapDispatchToProps = dispatch => {
  return {
    refreshData: rows => dispatch(refreshData(rows)),
    selectItem: selected => dispatch(selectItem(selected)),
  };
};

const VirtualTable = connect(mapStateToProps, mapDispatchToProps)(ConnectedVirtualTable);

export default VirtualTable;
